html {
  font-size: $font-size-root;
}

body {
  font-family: $font-family-sans-serif;
  font-size: $font-size-base;
  line-height: $line-height-base;
  color: $body-color;
  overflow-x: hidden;
}

a {
  color: var(--color-primary);
}

small {
  font-size: 9px;
}

img {
  max-width: 100%;
}

.container {
  width: calc(100% - #{$grid-gutter-width} * 2);

  .container {
    width: 100%;
  }
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

hr.styled {
  border: 0;
  height: 1px;
  background: #ccc;
  background-image: linear-gradient(to right, #ccc);
  margin: 40px 0;
}
